.common {
  border-radius:50%;
  border:none;
  display:flex;
  flex-direction:row;
  justify-content:center;
  align-items:center;
}
.active {
  background:linear-gradient(231.58deg, #f83600 -53.35%, #f9d423 297.76%);
}
.default {
  background:#fff;
  filter:drop-shadow(0px 2px 4px rgba(191, 196, 201, 0.2)) drop-shadow(0px 3px 4px rgba(191, 196, 201, 0.18)) drop-shadow(0px 1px 5px rgba(191, 196, 201, 0.24));
}
.default:hover {
  filter:drop-shadow(0px 2px 5px rgba(54, 54, 54, 0.1));
}
.default:active {
  filter:drop-shadow(0px 3px 4px rgba(222, 125, 11, 0.2)) drop-shadow(0px 1px 10px rgba(249, 89, 8, 0.2));
}
.withChildren {
  padding:8px 16px;
  column-gap:8px;
  border-radius:59px;
}
.largeSizeButton {
  min-width:38px;
  min-height:38px;
}
.smallSizeButton {
  min-width:32px;
  min-height:32px;
}
