.container {
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  row-gap:16px;
}
.bigSlider {
  width:445px;
  height:593px;
}
.smallSlider {
  width:100%;
  height:86px;
}
.slide {
  border:1px solid #a7a7a7;
  border-radius:3px;
}
.activeSlide {
  border:1px solid #ff5253;
  border-radius:3px;
}
.imgBig {
  height:100%;
  width:100%;
  border-radius:10px;
  border:1px solid #a7a7a7;
}
.imgSmall {
  height:100%;
  width:100%;
  border-radius:3px;
}
.slide:hover {
  cursor:pointer;
}
.paginationActive {
  background:#363636;
}

@media (max-width:992px) {
  .bigSlider {
    height:238px;
    width:136px;
  }
  .imgBig {
    height:198px;
    border-radius:3px;
  }
}

@media (max-width:576px) {
  .bigSlider {
    height:300px;
    width:188px;
  }
  .imgBig {
    height:260px;
  }
}
