@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap');
body {
  height:100%;
  justify-content:center;
  scroll-behavior:smooth;
  text-rendering:optimizeSpeed;
  font-family:'Montserrat', sans-serif;
  font-size:14px;
  line-height:18px;
  font-weight:400;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale;
  background-color:#ffffff;
  color:#363636;
  letter-spacing:0.1px;
}
*,
*:before,
*:after {
  box-sizing:border-box;
}
ul,
ol {
  margin:0;
  padding:0;
  list-style:none;
}
body,
h1,
h2,
h3,
h4,
h5,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin:0;
}
a {
  text-decoration:none;
  color:inherit;
}
a:not([class]) {
  text-decoration-skip-ink:auto;
}
img {
  max-width:100%;
  display:block;
}
article > * + * {
  margin-top:1em;
}
textarea,
input,
button,
textarea,
select {
  padding:0;
  font:inherit;
}
h1 {
  font-size:52px;
  line-height:64px;
  font-weight:500;
  letter-spacing:0.2px;
}
h2 {
  font-size:44px;
  line-height:54px;
  font-weight:600;
}
h3 {
  font-size:32px;
  line-height:40px;
  font-weight:700;
}
h4 {
  font-size:24px;
  line-height:30px;
  font-weight:700;
}
h5 {
  font-size:18px;
  line-height:28px;
  font-weight:700;
}

@media (max-width:576px) {
  h1 {
    font-size:32px;
    line-height:40px;
    font-weight:500;
  }
  h2 {
    font-size:26px;
    line-height:36px;
    font-weight:500;
  }
  h3 {
    font-size:18px;
    line-height:28px;
    font-weight:700;
  }
}
.swiper-pagination {
  margin-top:50px;
}
.swiper-pagination-bullet {
  opacity:1;
  width:7px;
  height:7px;
  background:#bfc4c9;
}
.swiper-pagination-bullet-active {
  width:9px;
  height:9px;
  background:#363636;
}
.swiper-slide-thumb-active {
  position:relative ;
  border:1px solid #f83600 ;
  border-radius:3px ;
}
