.container {
  display:flex;
  flex-direction:row;
  column-gap:146px;
}
.column {
  display:flex;
  flex-direction:column;
  row-gap:16px;
}
.leftColumn {
  min-width:329px;
}

@media (max-width:992px) {
  .container {
    column-gap:30px;
  }
  .leftColumn {
    min-width:305px;
  }
}

@media (max-width:790px) {
  .container {
    flex-direction:column;
    row-gap:16px;
  }
}
