.layout {
  max-width:1440px;
  padding:0 157px;
  width:100%;
  margin:0 auto;
}
.position {
  width:100%;
  display:flex;
  flex-direction:row;
  justify-content:center;
}
.container {
  min-height:100vh;
  min-width:280px;
  display:flex;
  flex-direction:column;
  justify-content:space-between;
  width:100%;
}
.main {
  flex-grow:1;
}


@media (max-width:1110px) {
  .layout {
    padding:0 64px;
  }
}

@media (max-width:576px) {
  .layout {
    padding:0 16px;
  }
  ::-webkit-scrollbar {
    width:0;
    height:0;
  }
}
