.container {
  width:100%;
  display:flex;
  flex-direction:row;
  justify-content:space-between;
}
.viewBlock {
  display:flex;
  flex-direction:row;
  column-gap:16px;
}
.filterBlock {
  display:flex;
  flex-direction:row;
  column-gap:16.9px;
  margin-right:16px;
}
.sortButtonText {
  color:#a7a7a7;
}
.inputMobile {
  width:100%;
  margin:3px 0;
}
.searchButton {
  display:none;
}

@media (max-width:700px) {
  .sortButtonText {
    display:none;
  }
  .buttonSort {
    padding:0 0;
  }
}

@media (max-width:774px) {
  .inputDesktop {
    width:274px;
  }
}

@media (max-width:470px) {
  .searchButton {
    display:flex;
  }
  .inputDesktop {
    display:none;
  }
  .container {
    padding:3px 0;
  }
}
