.contentContainer {
  min-width:613px;
  display:flex;
  flex-direction:column;
  row-gap:24px;
}
.emptyDataTextContainer {
  width:100%;
  height:100%;
  display:flex;
  flex-direction:row;
  justify-content:center;
  padding-top:175px;
}
.emptyDataText {
  text-align:center;
  color:#A7A7A7;
}
.booksContainer {
  display:flex;
  flex-direction:row;
  justify-content:space-between;
  flex-wrap:wrap;
  row-gap:24px;
  column-gap:20px;
}

@media (max-width:992px) {
  .contentContainer {
    min-width:280px;
  }
  .emptyDataTextContainer {
    padding-top:200px;
  }
}

@media (max-width:758px) {
  .booksContainer {
    justify-content:space-around;
  }
}

@media (max-width:470px) {
  .contentContainer {
    row-gap:16px;
  }
  .emptyDataTextContainer {
    padding-top:120px;
  }
}
